function slider({slide, nextArrow, prevArrow, wrapper, field}) {

    const slides = document.querySelectorAll(slide),
          prev = document.querySelector(prevArrow),
          next = document.querySelector(nextArrow),
          slidesWrapper = document.querySelector(wrapper),
          slidesField = document.querySelector(field);         
    
    const currentWidth = document.documentElement.clientWidth;                              //     (    )

    function deleteNotDigits(str) {                                                         //   ,            
        return +str.replace(/\D/g, '')                                                      // replace -         , +    
    }

    let pbs = 0;                                                                            //     (       )

    function placeBetweenSlides(field, slides) {                                            //        (       )
        const fieldWidth = deleteNotDigits(window.getComputedStyle(field).width);           //      
        let allSlideWidth = 0;                                                              //     

        slides.forEach(item => {
            allSlideWidth += item.offsetWidth;                                              //    
        });

        pbs = (fieldWidth - allSlideWidth) / (slides.length - 1);                           //      
    }

    placeBetweenSlides(slidesField, slides);

    let offset = 0,                                                                         //       
        numHideSlideNext = 0,                                                               //    
        numHideSlidePrev = 0;

    slidesField.style.transition = '0.5s all';                                              //     transition
    slidesWrapper.style.overflow = 'hidden';                                                //   ,     slidesWrapper,  .            


    function slideNext(numSlides, slide) {                                                  //     . : numSlides - -   , slide -     
        let slideWidth = 0;                                                                 //   

        if (numHideSlideNext == 0 || numHideSlideNext == numSlides) {                       //    .  0 -   ,  numSlides -   ""

            slideWidth = slide[numSlides].offsetWidth;                                      //    
            numHideSlideNext = numSlides + 1;                                               //     
            offset += slideWidth + pbs;                                                     //        +   
            numHideSlidePrev++;                                                             //  1    ,     ""

        } else if (numHideSlideNext > 0 && numHideSlideNext != slide.length) {              //     (       )

            slideWidth = slide[numHideSlideNext].offsetWidth;                               //    
            numHideSlideNext++;                                                             //     
            offset += slideWidth + pbs;                                                     //        +   
            numHideSlidePrev++;                                                             //  1    ,     ""

        } else if (numHideSlideNext == slide.length) {                                      //    

            offset = 0;                                                                     //     
            numHideSlideNext = 0;                                                           //     ""   
            numHideSlidePrev = 0;                                                           //     ""   
        }
    }   

    next.addEventListener('click', () => {
        if (currentWidth <= 1200 && currentWidth > 991) {                                   //      767 
            slideNext(2, slides);                                                           //  2   3
        } else if (currentWidth <= 991) {
            slideNext(1, slides);
        } else {
            slideNext(3, slides);
        }   

        slidesField.style.transform = `translateX(-${offset}px)`;                           //        
    });

    function slidePrev(numSlides, slide) {                                                  //     . : numSlides - -   , slide -     
        let slideWidth = 0;                                                                 //   

        if (numHideSlidePrev == 0) {                                                        //    

            for (let i = slides.length; i > numSlides; i--) {                               //    ,      
                slideWidth += slide[i - 1].offsetWidth;                                     //    
            }

            numHideSlidePrev = slides.length - numSlides;                                   //    ""   
            offset += slideWidth + pbs * (slides.length - numSlides);                       //    
            numHideSlideNext = slide.length;                                                //    ""   
            console.log(numHideSlidePrev);

        } else if (numHideSlidePrev > 0) {                                                  //     (       )

            slideWidth = slide[numHideSlidePrev - 1].offsetWidth;                           //    
            numHideSlidePrev--;                                                             //     "" 
            offset -= slideWidth + pbs;                                                     //        +   
            numHideSlideNext--;                                                             //     ""

            if (offset < 0) {
                offset = 0;
            }
        }
    }

    prev.addEventListener('click', () => {
        if (currentWidth <= 1200 && currentWidth > 991) {                                   //      767 
            slidePrev(2, slides);                                                           //  2   3
        } else if (currentWidth <= 991) {
            slidePrev(1, slides);
        } else {
            slidePrev(3, slides);
        }   
        
        slidesField.style.transform = `translateX(-${offset}px)`;                           //        
    });
}

export default slider;                                                 