import {postData} from '../services/requests';                                          //       

function form () {                                                               

    const forms = document.getElementsByTagName('form');                                //   

    const message = {                                                                   //    
        loading: '...',
        success: '!     ',
        failure: '-   ...',
        spinner: 'icons/Spinner-1s-75px.svg',                                           //   
    };

    forms.forEach(form => {                                                             //  

        form.addEventListener('submit', (e) => {                                        //         
            e.preventDefault();                                                         //     ( )

            const statusMessageSpnr = document.createElement('img'),                    //             
                statusMessageText = document.createElement('div');                      //             div  

            statusMessageSpnr.src = message.spinner;                                    //     src
            statusMessageSpnr.style.cssText = `
                display: block;
                margin: 0 auto;
            `;                                                                          //   ,    
            statusMessageText.textContent = message.loading;                            //    div   
            statusMessageText.classList.add('message', 'mt-0px');                       //   
            form.append(statusMessageSpnr);                                             //      
            form.append(statusMessageText);                                             //      
            
            const formData = new FormData(form);                                        //     FormData.    ,   .          "name",     

            postData('./server.php', formData)                                          //  
            .then(data => {                                                             //    , data -    
                statusMessageSpnr.remove();                                             //  
                statusMessageText.remove();                                             //     
                showThanksModal(message.success);                                       //  ,      
                console.log(data);                                                      //   ,   
            })
            .catch(data => {  
                console.log(data);                                                      //  -     
                statusMessageSpnr.remove();                                             //  
                statusMessageText.remove();                                             //     
                showThanksModal(message.failure);                                       //    
            })
            .finally(() => {                                                            //           , then  catch
                form.reset();                                                           //    
            });
        
            function showThanksModal(message) {                                         //       
                const thanksModal = document.createElement('div');                      //  
                    
                thanksModal.classList.add('message')                                    //    
                thanksModal.textContent = message;                                      //    div
                form.append(thanksModal);                                               //   div  
            
                setTimeout(() => {
                    thanksModal.remove();                                               //      4 
                }, 4000);        
            }
        });
    });     
};

export default form;